.horizontal_item {
    flex-shrink: 0;
    width: 80px;
    height: 114px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 12px;
}
.horizontal_item:first-child{
    padding-left: 4px;
}
.horizontal_item:last-child{
    padding-right: 4px;
}